import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'HomeList',
      component: function (resove) {
        require(['./pages/HomeList.vue'], resove)
      }
    },
    {
      path: '/searchList',
      name: 'SearchList',
      component: function (resove) {
        require(['./pages/SearchList.vue'], resove)
      }
    },
    {
      path: '/details/:id',
      name: 'Details',
      component: function (resove) {
        require(['./pages/Details.vue'], resove)
      }
    },
    {
      path: '/scroll',
      name: 'Scroll',
      component: function (resove) {
        require(['./pages/Scroll.vue'], resove)
      }
    }
  ]
})



